@font-face {
    font-family: 'Barlow Condensed medium';
    src: url('../src/fonts/BarlowCondensed-Bold.ttf') format('truetype');
  }

  @font-face {
    font-family: 'Barlow Condensed';
    src: url('../src/fonts/BarlowCondensed-Thin.ttf') format('truetype');
  }

  @font-face {
    font-family: 'Bellefair';
    src: url('../src/fonts/Bellefair-Regular.ttf') format('truetype');
}



.h{
    margin-top: 3.6vw;
}

.CrewPage {
    display: flex;
    flex-direction: column;
    position: fixed;


    background-image: url("/images/crew.svg");
    height: 100vh;
    width: 100vw;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.title{
    display: flex;
    font-family: 'Bellefair';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 37px;
    /* identical to box height */

    text-transform: uppercase;

    color: #FFFFFF;

    mix-blend-mode: normal;
    opacity: 0.5;
    margin-bottom: 1.2vw;
}

.name{
    font-family: 'Bellefair';
    font-style: normal;
    font-weight: 400;
    font-size: 56px;
    line-height: 64px;
    text-transform: uppercase;

    color: #FFFFFF;
    margin-bottom: 1.8vw;
}

.about{ 
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    /* or 178% */

    width: 444px;
    height: 128px;

    color: #D0D6F9;
    // margin-bottom: 12vw;

}

.crew{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    margin-left: 13vw;

}

.Img{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    margin-top: 12vw;
    margin-right: 16vw;

}

.crewImg{
    background-image: url("/images/Douglas.svg");
    margin-left: 50%;


    position: absolute;
    width: 568.07px;
    height: 712px;

    background-repeat: no-repeat;
    background-position: center center;

}
.info{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;

    margin-bottom: 1vw;
}

    .sidebar {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-top: 5vw;
        margin-bottom: -4vw;
        
      }
      
      .circle-btn {
        display: inline-flex;
        justify-content: space-between;
        align-items: center;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        margin-right: 1.6vw;

        background-color: #fff;
        opacity: 0.17;
        border: none;
        outline: none;
      }
      
      .circle-btn.active {
        opacity: 1;
      }
      
      .circle-btn:focus {
        outline: none;
      }
      
      .circle-btn:hover {
        opacity: 0.5;
      }
      